import './App.css';
import axios from 'axios'
import {useState,useEffect} from 'react'

//todo
//add the new build to backend
//reflect importance upon in frontend upon clicking

function App() {
  const [notes, setnotes] = useState([])
  const[NewNote,setNewNote] = useState('')
  const[update,setupdate] = useState(true)
  const[displayNote,setdisplayNote] = useState([])
  const[showAll, setShowAll] = useState(true)


  const notesToShow = showAll? notes: notes.filter(note => note.important === true)


  const handleChange = (e) => {
    setNewNote(e.target.value)
  }

//pls forgive me..there must be a better way to do this
  const handleSubmit = () => {
    setupdate(!update)
    setnotes([])
    addnoteToDb()
  }

  const addnoteToDb = () => {
    axios.post('/api/notes',{content:NewNote})
    .then(res => {
      console.log(res)})
  }

  // const toggleImportance = (note) => {
  //   axios.put(`/api/notes/${note.id}`,{
  //     content:note.content,
  //     important:!note.important
  //   }).then(res => {console.log(res)})
  //   .catch(err => console.log(err))
  // }

  const toggleImportance = id => {
    const url = `/api/notes/${id}`
    console.log(notes)
    console.log(displayNote)
    const note = notes.find(n => n.id === id)
    console.log(note)
    const changedNote = { ...note, important: !note.important }
  
    axios.put(url, changedNote).then(response => {
      setnotes(notes.map(note => note.id !== id ? note : response.data))
    })
  }

  const getNotes = () => {
    axios.get('/api/notes')
    .then(res => {
      // console.log(res.data)
      setnotes(notes.concat(res.data))
      setdisplayNote(displayNote.concat(res.data))
    })
  }
// eslint-disable-next-line
    useEffect(getNotes,[update])

  return (
    <div className="App">
    <h1>Notes</h1>
    <div className="container">
    <button onClick={() => setShowAll(!showAll)} className="filter">          
        Show {showAll ? 'important' : 'all' }
    </button>
    <ul>
    {notesToShow.map(n => <li key={n.id}> {n.content} 
    <button className={n.important? "important":"notImportant"} onClick={() => toggleImportance(n.id)}>{n.important? "make not important":"make important"}</button> </li>)}
    </ul>
    <div className="input-notes">
    <input type="text" onChange={handleChange}/>
    <button className="save" onClick={handleSubmit}>save</button>
    </div>
    </div>
    </div>
  );
}

export default App;
